/* profile */

.profile {
    grid-area: c;
    padding: 3%;
}

/* profile logo */

.profile_logo {
    display: inline-block;
    width: 150px;
    margin-left: 5%;
    vertical-align: bottom;
}

.profile_logo img {
    position: absolute;
    z-index: -1;
    width: 150px;
    height: 150px;
    border-radius: 100px;
}

.change_logo__wrapper {
    width: 150px;
    height: 150px;
}

.change_logo__input {
    position: absolute;
    opacity: 0;
    z-index: -1;
}

.change_logo__label {
    display: block;
    background-image: url('../assets/images/updateLogoIcon.png');
    background-repeat: no-repeat;
    background-size: 32px;
    background-position: 50% 50%;
    width: 150px;
    height: 150px;
    opacity: 0;
}

.profile_logo:hover img {
    opacity: 0.3;
}

.profile_logo:hover .change_logo__label {
    cursor: pointer;
    opacity: 1;
}


/* profile name */

.profile_name {
    display: inline-block;
    margin-left: 5%;
}

/* profile status */

.profile_status {
    position: relative;
    font-size: 14px;
    color: #000;
    margin-left: 22%;
}

.profile_status span {
    margin-right: 10px;
}

.profile_status__paragraph {
    display: inline-block;
    color: rgb(1, 1, 88);
    cursor: pointer;
}

.profile_status__paragraph p {
    font-size: 18px;
    line-height: 20px;
    margin-block-start: 0em;
    margin-block-end: 0em;
}

.profile_status__form {
    display: inline-block;
}

.profile_status__input {
    display: inline-block;
    height: 20px;
    color: rgb(1, 1, 88);
    border-radius: 5px;
    border: 1px solid #d4d4d4;
    margin-right: 10px;
}

/* profile description */

.profile_description {
    width: 40%;
    margin-left: 230px;
}

/* profile write post */
.write_post {
    margin-left: 50px;
    margin-top: 100px;
}

.write_post__input {
    display: inline-block;
    padding-left: 10px;
    width: 50%;
    height: 30px;
    border-radius: 5px;
    border: 1px solid #d4d4d4;
    margin-right: 10px;
}

.write_post__button {
    display: inline-block;
    line-height: 30px;
    width: 10%;
    color: #fff;
    border-radius: 5px;
    border: 1px solid #d4d4d4;
    background-color: #404f80;
    transition: 0.5s ease;
    cursor: pointer;
}

.write_post__button:hover {
    background-color: #2447b8;
    transition: 0.5s ease;
}

/* profile posts */

.profile_posts {
    margin-top: 30px;
    margin-left: 50px;
}

.profile_posts__title {
    margin-bottom: 20px;
}

.post_block {
    width: 80%;
    margin-left: 20px;
}

.authors_post {
    line-height: 10px;
}

.post_block__profile_logo {
    display: inline-block;
    width: 8%;
    margin-right: 1%;
}

.post_block__profile_logo img {
    width: 100%;
    vertical-align: middle;
    border-radius: 50px;
}

.post_block__profile_name {
    display: inline-block;
}

.post_message {
    margin-left: 60px;
    padding: 10px;
    box-shadow: 0 0 3px #bdbdbd;
}

@media screen and (max-width: 1000px) {
    .profile_status {
        margin-left: 40%;
    }

    .profile_posts {
        margin-left: 25px;
    }

    .post_block__profile_logo {
        width: 13%;
    }

    .write_post__input {
        width: 70%;
    }

    .write_post__button {
        width: 20%;
    }
}

@media screen and (max-width: 600px) {
    .profile_status {
        position: absolute;
        font-size: 12px;
        margin-top: 120px;
        margin-left: 30px;
    }

    .profile_posts {
        margin-left: 0;
    }

    .profile_name {
        display: block;
        margin-top: 50px;
        margin-left: 30px;
        font-size: 14px;
    }

    .write_post {
        margin-left: 0;
    }

    .profile_logo {
        width: 100px;
        margin-left: 5%;
    }
    
    .profile_logo img {
        width: 100px;
        height: 100px;
    }
    
    .change_logo__wrapper {
        width: 100px;
        height: 100px;
    }
    .change_logo__label{
        width: 100px;
        height: 100px;
    }

    .profile_status__paragraph p {
        font-size: 14px;
        line-height: 16px;
    }
    
    .profile_status__form {
        display: inline-block;
    }
    
    .profile_status__input {
        height: 16px;
    }
    .write_post__input {
        margin-right: 2px;
    }
    .write_post__button{
        font-size: 12px;
    }
}